<template>
  <div>
    <fTitle>
      <template #title>MessageBox 弹框</template>
      <template #introduce>模拟系统的消息提示框而实现的一套模态对话框组件，用于消息提示、确认消息和提交内容。</template>
    </fTitle>
    <sTitle>
      <template #title>消息提示</template>
      <template #introduce>当用户进行操作时会被触发，该对话框中断用户操作，直到用户确认知晓后才可关闭。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="10">
<div class="grid-content"><h3>拟alert</h3><el-button type="text" @click="open1">点击打开 Message Box</el-button></div>
    </el-col>
    <el-col :span="10">
<div class="grid-content"><h3>拟confirm</h3><el-button type="text" @click="open2">点击打开 Message Box</el-button></div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
    <el-col :span="10">
<div class="grid-content"><h3>拟prompt</h3><el-button type="text" @click="open3">点击打开 Message Box</el-button></div>
    </el-col>
    <el-col :span="10">
<div class="grid-content"><h3>msgbox自定义</h3><el-button type="text" @click="open4">点击打开 Message Box</el-button></div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  methods: {
    open1 () {
      this.$alert('这是一段内容', '标题名称', {
        confirmButtonText: '确定',
        callback: action => {
          this.$message({
            type: 'info',
            message: `action: ${action}`
          })
        }
      })
    },
    open2 () {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    open3 () {
      this.$prompt('请输入邮箱', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: '邮箱格式不正确'
      }).then(({ value }) => {
        this.$message({
          type: 'success',
          message: '你的邮箱是: ' + value
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        })
      })
    },
    open4 () {
      const h = this.$createElement
      this.$msgbox({
        title: '消息',
        message: h('p', null, [
          h('span', null, '内容可以是 '),
          h('i', { style: 'color: teal' }, 'VNode')
        ]),
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            instance.confirmButtonText = '执行中...'
            setTimeout(() => {
              done()
              setTimeout(() => {
                instance.confirmButtonLoading = false
              }, 300)
            }, 3000)
          } else {
            done()
          }
        }
      }).then(action => {
        this.$message({
          type: 'info',
          message: 'action: ' + action
        })
      })
        .catch(() => {})
    }
  }

}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;}
    }}
</style>
